import "./App.css";
import React from "react";
import { Table } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
// import store, {changeName, increase} from './store.js'
import { addCount, decreaseCount, deleteItem } from "./store";
import { Link } from "react-router-dom";
import { useState } from "react";

interface RootState{
  cart:{
  id:number;
  mainImg:string;
  itemTitle:string;
  price:number;
  count:number;
  }[];
}

function Cart():JSX.Element {
  const state = useSelector((state:RootState) => state);
  // dispatch  store.js   
  const dispatch = useDispatch();
  let totalsum = 0;
  for (let i = 0; i < state.cart.length; i++) {
    totalsum += state.cart[i].price * state.cart[i].count;
  }

  //     
  const [checkItems, setCheckItems] = useState<number[]>([]);

  //     
  const handleSingleCheck = (checked:boolean, id:number) => {
    if (checked) {
      setCheckItems((prev:number[]) => [...prev, id]); //       
    } else {
      setCheckItems(checkItems.filter((el) => el !== id)); //    ,   () 
    }
  };

  //    
  const handleAllCheck = (checked:boolean) => {
    if (checked) {
      //   ,   (id)   
      const idArray : number[] = [];
      state.cart.forEach((el) => idArray.push(el.id));
      setCheckItems(idArray);
    } else {
      setCheckItems([]); //    ,   
    }
  };

  function deleteHandler() {
    dispatch({
      type: deleteItem,
      payload: { checkItems: checkItems},
    });
    // 
    setCheckItems([]);
  }

  return (
    <>
      <div className="bodyContent">
        <div className="orderTitle">
          <h2></h2>
        </div>
        <div className="orderProcess">
          <ul>
            <li style={{ color: "#000" }}>01 </li>
            <li style={{ color: "#000" }}>&gt;</li>
            <li>02   </li>
            <li>&gt;</li>
            <li>03 </li>
          </ul>
        </div>
        <div className="ordertableBox">
          <Table style={{ width: "100%", textAlign: "center" }}>
            <colgroup>
              <col style={{ width: "8%" }}></col>
              <col></col>
              <col style={{ width: "12%" }}></col>
              <col style={{ width: "18%" }}></col>
              <col style={{ width: "15%" }}></col>
            </colgroup>
            <thead>
              <tr className="orderThead">
                <th>
                  <input
                    type="checkbox"
                    id="totalchkbx"
                    onChange={(e) => handleAllCheck(e.target.checked)}
                    checked={
                      checkItems.length === state.cart.length ? true : false
                    }
                  ></input>
                  <label htmlFor="totalchkbx"></label>
                </th>
                <th>/</th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {state.cart.map((a, i) => (
                <tr key={i} style={{ height: "130px" }}>
                  <td>
                    <input
                      type="checkbox"
                      id={`checkbox-${i}`}
                      onChange={(e) =>
                        handleSingleCheck(e.target.checked, state.cart[i].id)
                      }
                      checked={
                        checkItems.includes(state.cart[i].id) ? true : false
                      }
                    ></input>
                    <label htmlFor={`checkbox-${i}`}></label>
                  </td>
                  <td
                    style={{
                      borderRight: "1px solid#dedede",
                      textAlign: "left",
                    }}
                  >
                    <Link
                      to={`/detail/${state.cart[i].id}`}
                      className="cartInfoBox"
                    >
                      <img
                        src={`${state.cart[i].mainImg}`}
                        style={{ width: "70px", height: "70px" }}
                      alt=""/>
                      <div className="cartInfo">
                        <span className="cartCupon"></span>
                        <p className="cartTitle">{`${state.cart[i].itemTitle}`}</p>
                      </div>
                    </Link>
                  </td>
                  <td style={{ borderRight: "1px solid#dedede" }}>
                    <div className="cartUpDownBtn">
                      <button
                        onClick={() => {
                          dispatch(addCount(state.cart[i].id));
                        }}
                        style={{ marginRight: "10px" }}
                        className="cartBtn"
                      >
                        +
                      </button>
                      {state.cart[i].count}
                      <button
                        onClick={() => {
                          dispatch(decreaseCount(state.cart[i].id));
                        }}
                        style={{ marginLeft: "10px" }}
                        className="cartBtn"
                      >
                        -
                      </button>
                    </div>
                  </td>
                  <td style={{ borderRight: "1px solid#dedede" }}>
                    <p className="cartPrice">
                      {(
                        state.cart[i].price *
                        state.cart[i].count
                      )
                        .toString()
                        .replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                    </p>
                    <span className="cartPrice"></span>
                  </td>
                  <td style={{ borderRight: "1px solid#dedede" }}>
                    <div className="cartBtnBox">
                      <button className="bottomBtn"></button>
                      <button className="bottomOptBtn"></button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </div>
        <div className="bottomBtnBox">
          <Link to={"/"}>
            <button className="bottomBtn"> </button>
          </Link>
          <button className="bottomBtn" onClick={deleteHandler}>
            
          </button>
        </div>
        <div className="bottomTotalBox">
          <ul className="bottomTotalUl">
            <li className="bottomTotalLi">
              <span>  </span>
              <div>
                <span style={{ fontWeight: "bold" }}>
                  {totalsum.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                </span>
                <span></span>
              </div>
            </li>
            <li className="bottomTotalLi">
              <img
                src="../img/icon4.png"
                alt=""
                className="bottomOrderIcon"
              />
            </li>
            <li className="bottomTotalLi">
              <span></span>
              <div>
                <span style={{ fontWeight: "bold" }}>0</span>
                <span></span>
              </div>
            </li>
            <li className="bottomTotalLi">
              <img
                src="../img/icon5.png"
                alt=""
                className="bottomOrderIcon"
              />
            </li>
            <li className="bottomTotalLi">
              <span></span>
              <div>
                <span style={{ fontWeight: "bold" }}>
                  {totalsum.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                </span>
                <span></span>
              </div>
            </li>
          </ul>
        </div>

        <div className="bottomOrderBtnBox">
          <button className="bottomOrderBtn1">  </button>
          <button className="bottomOrderBtn2">  </button>
        </div>
      </div>
    </>
  );
}

export default Cart;
