import './App.css';
import Form from 'react-bootstrap/Form';
import { Button } from "react-bootstrap";



function Login(): JSX.Element{
    
    return (
      <div>
        <div className="bodyContent">
          <div className="loginTitle">
            <h2></h2>
          </div>
          <div className="loginInfoBox">
            <Form.Control
              className="loginInfo"
              type="text"
              placeholder=""
            />
            <Form.Control
              className="loginInfo"
              type="text"
              placeholder=""
            />
            <Button className="loginBtn" variant="dark">
              
            </Button>
            <div className="loginId">
              <span>
                <input type="checkbox" id="savedId"></input>
                <label htmlFor="savedId"></label>
                <span className="savedId"></span>
              </span>
              <span className="findId"> / </span>
            </div>
            <Button
              className="loginBtn"
              variant="warning"
              style={{ margin: "15px 0px" }}
            >
              <img
                src="../img/kakao.png"
                alt=""
                style={{ width: "20px", height: "20px", marginRight: "10px" }}
              />
               1 /
            </Button>
            <div className="newMenberbox">
              <span className="newMember"></span>
              <span className="newMember"> </span>
            </div>
          </div>
        </div>
      </div>
    );
}

export default Login;
