import "../App.css";
import { useParams } from "react-router-dom";
import { Nav, Navbar, Table } from 'react-bootstrap';
import { DetailProps } from '../DetailProduct'

function DetailBox(props:DetailProps):JSX.Element {
  let { id } = useParams();
  return (
    <div>
      <div className="detailContentNav" id="detailBox">
        <Navbar className="detailNavbar">
          <Nav.Link
            className="detailNavText"
            style={{ borderBottom: "2px solid #000", color: "#000" }}
            href="#detailBox"
          >
            
          </Nav.Link>
          <Nav.Link
            className="detailNavText"
            style={{ color: "#000" }}
            href="#reviewBox"
          >
            
          </Nav.Link>
          <Nav.Link
            className="detailNavText"
            style={{ color: "#000" }}
            href="#qnaBox"
          >
            
          </Nav.Link>
          <Nav.Link
            className="detailNavText"
            style={{ color: "#000" }}
            href="#refundBox"
          >
            //
          </Nav.Link>
        </Navbar>
      </div>
      <div className="detailBox">
        <div className="detailContentImgBox">
        {props.detail[Number(id) - 1].detailBox_Imgset.map((ele, i) => {
            return <img key={i} src={ele.detailBoxImg} alt="" />;
          })}
        </div>
        <div className="detailContentText">
          <h3> </h3>
          <Table className="detailTableStyle">
            <colgroup>
              <col style={{ width: "20%" }}></col>
              <col style={{ width: "80%" }}></col>
            </colgroup>
            <tbody>
              <tr>
                <th> </th>
                <td>
                  {props.detail[Number(id) - 1].detailBox_Info}
                </td>
              </tr>
              <tr>
                <th></th>
                <td>{props.detail[Number(id) - 1].detailBox_color}</td>
              </tr>
              <tr>
                <th></th>
                <td> </td>
              </tr>
              <tr>
                <th>(/)</th>
                <td>LF</td>
              </tr>
              <tr>
                <th></th>
                <td></td>
              </tr>
              <tr>
                <th> </th>
                <td></td>
              </tr>
              <tr>
                <th></th>
                <td>
                  6/      
                   
                </td>
              </tr>
              <tr>
                <th>A/S  </th>
                <td>LF  1544-5114</td>
              </tr>
              <tr>
                <th></th>
                <td></td>
              </tr>
              <tr>
                <th></th>
                <td></td>
              </tr>
              <tr>
                <th></th>
                <td></td>
              </tr>
              <tr>
                <th></th>
                <td></td>
              </tr>
              <tr>
                <th>KC  </th>
                <td></td>
              </tr>
              <tr>
                <th></th>
                <td></td>
              </tr>
              <tr style={{ borderBottom: "1px solid #000" }}>
                <th> __</th>
                <td></td>
              </tr>
            </tbody>
          </Table>
        </div>
      </div>
    </div>
  );
}

export default DetailBox;
